<template>
  <SubNavigationFrame :title="$route.meta.title" :breadcrumb="breadcrumbs">
    <div class="article-category-page-container" slot="content">

        <Tabs>
          <TabPane label="播放器">
            <div class="bg" :class="{rotatey:active}" ><img :src="$store.state.song_image_url" alt=""></div>
            <div class="s1" >
                    <h1>{{$store.state.songs}}</h1>
                    <h4>{{$store.state.singer}}</h4>
              <ButtonGroup size="large" class="btn" >
                <Button class="back" type="primary" icon="ios-skip-backward"   ></Button>
                <Button class="back" type="primary" :icon="icon" ></Button>
                <Button class="back" type="primary" icon="ios-skip-forward"  ></Button>

              </ButtonGroup>
              <audio id="ad" ref="ad"  :src="$store.state.musicurl"  ></audio>
              <h6>{{$store.state.currentmin+':'+$store.state.currentsec}}/{{$store.state.minutes+':'+$store.state.seconds}}</h6>
              <Slider class="slider" v-model="$store.state.value4"
                      @on-change="nextTick"
                      :step="1"></Slider>
            </div>

          </TabPane>
          <TabPane label="发现音乐">
            <Input icon="ios-search"  v-model="value" placeholder="音乐/视频/电台/用户" style="width: 300px" />
            <List>
              <ListItem>
                <ListItemMeta avatar="https://dev-file.iviewui.com/userinfoPDvn9gKWYihR24SpgC319vXY8qniCqj4/avatar" title="This is title" description="This is description, this is description." />
              </ListItem>
            </List>
          </TabPane>
          <TabPane label="我的收藏">
              <List>
                <ListItem>
                  <ListItemMeta avatar="https://dev-file.iviewui.com/userinfoPDvn9gKWYihR24SpgC319vXY8qniCqj4/avatar" title="This is title" description="This is description, this is description." />
                </ListItem>
              </List>
          </TabPane>

        </Tabs>
    </div>
  </SubNavigationFrame>

</template>

<script>
  import SubNavigationFrame from '../../components/SubNavigationFrame/SubNavigationFrame';
  import Action from './action/musicIndex'

  const dataFactory = (params) => {
    return params ? params : {
      MusicCode: '',
      songs_name:'df',
      singer:'f',
      songs_id:'',
      song_file_url:'',
      song_image_url:'',
      songs_file_id:'',
      uploadStatus: null,
      uploadList: [],
      ShowOnIndex: false,
      edit: false
    }
  }

    export default {
        name: "musicIndex",
        components: {
          SubNavigationFrame,
      },
      data () {
        return {
          // value4:0,
          breadcrumbs:[
            {title: '我的音乐'},
            {title: this.$route.meta.title}
          ],
          // musicurl: '',
          tableData:[{}],
          value:"",
          active:false,
          createModal:dataFactory(),
          minutes:'00',
          seconds:'00',
          currentmin:'00',
          currentsec:'00',
          de:'',
          index:0,
          myVid: null,
          icon: 'ios-play-outline',
        }
      },

      mounted() {
        // this.myVid = this.$refs.ad;
        // this.showtime();
        // this.paramsShowHandler();

      },
      methods:{

        nextTick(e){
          
          this.$nextTick(()=>{
            let currenttime = (e / 100) * this.myVid.duration;
            this.myVid.currentTime = currenttime;
          })
        },
          //上一首歌曲
        before(){
          this.active=true
          this.icon="ios-pause"
          this.index--;
          if (this.index == -1) this.index = this.tableData.length - 1;
          this.$store.state.musicurl = this.tableData[this.index].song_file_url;
          this.$store.state.songs = this.tableData[this.index].songs_name;
          this.$store.state.singer = this.tableData[this.index].singer;
          this.myVid.load()
          setTimeout(() => {
            this.myVid.play();
          }, 100)
        },
          //下一首歌曲
        next(){
          this.active=true
          this.icon="ios-pause"
          this.index++;
          if (this.index == this.tableData.length) this.index = 0;
          this.$store.state.musicurl = this.tableData[this.index].song_file_url;
          this.$store.state.songs = this.tableData[this.index].songs_name;
          this.$store.state.singer = this.tableData[this.index].singer;
          this.myVid.load()
          setTimeout(() => {
            this.myVid.play();
          }, 100)
        },
        //显示当前时间
        showtime(){

          return this.myVid.ontimeupdate = () => {
            //当前时长
            let currenttime = this.myVid.currentTime;

            this.$store.state.currentmin = parseInt(currenttime / 60);
            this.$store.state.currentsec = parseInt(currenttime % 60);
            this.$store.state.currentmin=(this.$store.state.currentmin <10?("0"+this.$store.state.currentmin):this.$store.state.currentmin);
            this.$store.state.currentsec=(this.$store.state.currentsec <10?("0"+this.$store.state.currentsec):this.$store.state.currentsec);
            //总时长
            let time = this.myVid.duration;
            this.$store.state.minutes = parseInt(time / 60, 10);
            this.$store.state.seconds = parseInt(time % 60);
            this.$store.state.minutes=(this.$store.state.minutes <10?("0"+this.$store.state.minutes):this.$store.state.minutes);
            this.$store.state.seconds=(this.$store.state.seconds <10?("0"+this.$store.state.seconds):this.$store.state.seconds);
            this.$store.state.value4 = Math.floor((currenttime / (time / 100)))
            if (currenttime === time) {
              this.active=true
              this.icon="ios-pause"
              this.index++;
              if (this.index == this.tableData.length) this.index = 0;
              this.$store.state.musicurl = this.tableData[this.index].song_file_url;
              this.$store.state.songs = this.tableData[this.index].songs_name;
              this.$store.state.singer = this.tableData[this.index].singer;
              this.myVid.load()
              setTimeout(() => {
                this.myVid.play();
              }, 200)
            }
          }
        },

        rotate(){
            this.$refs.ad.play()
            if(this.active==false)
            {
              this.active=true
              this.icon="ios-pause";

              this.myVid.play();

            }
            else{
              this.active=false
              this.icon="ios-play-outline";
              this.myVid.pause();
            }
          },
        //
        // //删除参数详情
        // deleteDetailHandler(ID){
        //   if (!ID) return this.$Message.warning('参数ID为空，无法删除！！！');
        //   const modal = this.$Modal.confirm({
        //     title: '操作确认'
        //     ,icon: 'warning'
        //     ,content: '是否删除此参数'
        //     ,okText: '确定'
        //     ,showCancel: true
        //     ,loading: true
        //     ,onOk: () => {
        //       //发起删除参数请求
        //       Action.MusicDeleteFiles({
        //         PostContent: {
        //           songs_file_id: ID
        //         }
        //       }).then(result => {
        //         this.$Message.success('成功删除参数！！！');
        //         this.$Modal.remove()
        //         this.paramsShowHandler();
        //       })
        //         .catch(err => {
        //           this.$Modal.remove()
        //           this.$Message.error(err);
        //         });
        //     }
        //   });
        // },
        //根据参数权限码显示页面
        paramsShowHandler(){
          // let _filter = objectCopy(this.filter);
          // for (let i in _filter) {
          //   if (!_filter[i]) delete _filter[i]
          // }
          Action.musicGetList({
            // PostContent: {
            //   filter: _filter
            // }
          })
            .then(res => {
              this.tableData = res.map(item=>{
                item.song_image_url = `${REQUEST_URL.IndexImageDownload}${item.song_image_url}`;
                item.song_file_url = `${REQUEST_URL.IndexImageDownload}${item.song_file_url}`;
                return item;
              });

              this.$store.state.musicurl = this.tableData[this.index].song_file_url;
            })
            .catch(err => {
              console.log(err)
              this.$Message.error(err);
            })
        },

      },

    }
</script>

<style scoped>

  .bg{
    height: 300px;
    width: 300px;
    margin-left: 50px;
    margin-top: 50px;
    display: inline-block;
    overflow: hidden;
  }
  .bg img{
    height: 100%;
    width: 100%;
    border-radius:50%

  }
  .rotatey{
    -webkit-transform: rotate(360deg);
    animation: rotation 10s linear infinite;
    -moz-animation: rotation 10s linear infinite;
    -webkit-animation: rotation 10s linear infinite;
    -o-animation: rotation 10s linear infinite;
  }
  @-webkit-keyframes rotation{
    from {-webkit-transform: rotate(0deg);}
    to {-webkit-transform: rotate(360deg);}
  }
  .s1{
    height: 300px;
    width: 420px;
    display: inline-block;
    overflow: hidden;
  }

  h1,h6,h4{
    margin-top: 20px;
   text-align: center;
  }
  .slider{
    width: 360px;
    margin-left: 30px;
  }
  .btn{
    position: relative;
    top: 50%;
    left: 50%;
   margin: 50px 0 0 -50px;
  }

</style>



